import { Component, OnInit } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { AngularFireDatabase, AngularFireList ,  AngularFireObject} from '@angular/fire/database';
import {  Observable} from 'rxjs';
import { map } from 'rxjs/operators';
import {Router} from '@angular/router';


@Component({
  selector: 'app-test-remote-page',
  templateUrl: './test-remote-page.component.html',
  styleUrls: ['./test-remote-page.component.css']
})
export class TestRemotePageComponent implements OnInit {
  itemRef: AngularFireObject<any>;
  item: Observable<any>;
  testScoreRef: AngularFireList<any>;
  studentScore=[]
  btn='s'
  testLoadRef:AngularFireObject<any>;
  testLoaded;
  testsRef:AngularFireList<any>;
  noOfQue=0
  testTitle;
  CSVoptions = {
    fieldSeparator: ',',
    quoteStrings: '"',
    decimalseparator: '.',
    showLabels: true,
    headers: [],
    showTitle: true,
    title: '',
    useBom: false,
    removeNewLines: true,
    keys: ['name' ]
  };
  CSVData=[]
  constructor(public router:Router,db: AngularFireDatabase) {
  this.itemRef = db.object('statusTest');
    this.testsRef= db.list('TESTS')
    this.item = this.itemRef.valueChanges();
    this.testScoreRef = db.list('testScore')
    this.testScoreRef.snapshotChanges()
    .subscribe(actions => {
      this.studentScore=[]
      actions.forEach(action => {
        // console.log(action.type);
        // console.log(action.key);
        // console.log(action.payload.val());
        // this.studentScore.push({"name":action.payload.val().name,'totalScore':action.payload.val().totalScore , "neg":action.payload.val().neg, "opt":action.payload.val().opt})
      });
    });
    this.testLoadRef= db.object('loadedTest')

      this.testLoadRef.snapshotChanges().subscribe(test =>{
        this.testLoaded=test.payload.val()
        this.studentScore=[]
        this.noOfQue=test.payload.val().numberOfQuestion.total
        this.testTitle=test.payload.val().title

        test.payload.val().studentOptions.forEach(options=>{
          console.log(options)
          this.studentScore.push(options)
        })
        this.exportCSV()
      })
  }
testStatus(x){
    if(x==1){  this.testLoadRef.update({ startTest: "start" }); this.testLoaded.startTest="start";
    this.testsRef.update(this.testLoaded.key,{startTest: "start" })

    }
    if(x==2){  this.testLoadRef.update({ startTest: "wait" }); this.testLoaded.startTest="wait"
    this.testsRef.update(this.testLoaded.key,{startTest: "wait" })

    }
    if(x==3){
        this.testLoadRef.update({ startTest: "end" , testStatus:'complete'}); this.testLoaded.startTest="end";
        // console.log(this.testLoaded.key)
        this.testsRef.update(this.testLoaded.key,{startTest: "end" , Teststatus:'complete' })
       }
       if(x==4){
         this.testLoadRef.remove()
         this.router.navigate(['load'])
       }
       if(x==5){
         this.testLoadRef.update({ startTest: "wait" }); this.testLoaded.startTest="wait"
         this.testsRef.update(this.testLoaded.key,{startTest: "wait" })

       }
  }
  ngOnInit() {
  }
  exportCSV(){
    for(let j=0;j<this.noOfQue;j++){
    this.CSVoptions.keys.push((j+1).toString())
    }
    this.CSVoptions.headers=this.CSVoptions.keys

    for(let i = 0 ; i<this.studentScore.length;i++){
      let stuOpt=this.studentScore[i].opt
      this.CSVData[i]={
        name:this.studentScore[i].name,

      }
      if(Array.isArray(this.studentScore[i].opt)){
        for(let j=0;j<this.studentScore[i].opt.length;j++){
          if(this.studentScore[i].opt[j]!=undefined && this.studentScore[i].opt[j]!=null){

            this.CSVData[i][j+1]=this.studentScore[i].opt[j]
          }else(
            this.CSVData[i][j+1]=""
          )
        }
      }else{
      let  options = Object.entries(this.studentScore[i].opt)
      // for(let j=0;j< this.noOfQue;j++){
        // let index =options[j][0]
        for(let k=0;k< options.length;k++){
          // console.log(options[k][0])
          // if((j)==parseInt(options[k][0])){
            this.CSVData[i][k+1]=options[k][1]

          // }else{
          //   this.CSVData[i][j]=""
          // }
        }
        for(let k=0;k< this.noOfQue;k++){
          // console.log(options[k][0])
          // if((j)==parseInt(options[k][0])){
            if(this.CSVData[i][k+1]==undefined){
              this.CSVData[i][k+1]=""
            }

          // }else{
          //   this.CSVData[i][j]=""
          // }
        // }
      }
      }

    }

    console.log(this.CSVData)
  }
}
